.shopTitle {
  display: inline-block;
  margin-top: 25px;
  text-align: center;
  font-size: 15px;
  margin-bottom: 60px;
  background-color: aqua;
  width: 300px;
  border-radius: 20px;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
}

.products {
  margin-right: 0px;
  padding: 10;
  width: 90%;
  height: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  place-items: center;
}

.product {
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  width: 200px;
  height: 400px;
  margin: 30px;
  margin-bottom: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.product img {
  width: 200px;
}

.product .description {
  text-align: center;
}

.product:hover {
  transition: 0.3s ease-in;
  cursor: pointer;
}

.addToCartBttn {
  font-size: 15px;
  font-weight: 700;
  text-transform: capitalize;
  background-color: transparent;
  border: 2px solid rgb(19, 19, 19);
  min-width: 100px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 15px;
  margin-bottom: 20px;
}

.addToCartBttn:hover {
  background-color: rgb(19, 19, 19);
  color: white;
  cursor: pointer;
}
