/* Enhanced Navbar Styles */
.navbar {
  font-weight: 700;
  width: 100%;
  height: 80px;
  background-color: rgb(19, 19, 19); /* Consider adjusting color for contrast */
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 50px; /* Adds padding on both ends */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* Adds a subtle shadow for depth */
}
.navbar p {
  color: aliceblue;
  margin: 30px;
  padding: 30px;
  font-weight: 700;
  font-size: 20px;
}
.navbar img {
  width: 80px;
  display: flex;
  border-radius: 10px;
}
.links {
  text-transform: capitalize;
  display: flex;
  margin: 20px;
  width: 200px;
  align-items: center;
  height: 100%; /* Ensures link container matches navbar height */
}
.links .cart {
  background-color: brown;
  width: 60px;
  height: 40px;
  border-radius: 10px;
}
.links a {
  text-decoration: none;
  color: white;
  margin-left: 10px; /* Adjusted for better spacing */
  font-size: 22px; /* Adjust size as needed */
  padding: 10px 15px; /* Adds padding for a larger click area */
  border-radius: 5px; /* Softens the edges */
  transition: background-color 0.3s, color 0.3s; /* Smooth transition for hover effects */
}

.links a:hover,
.links a:focus {
  background-color: white; /* Background color on hover */
  color: rgb(19, 19, 19); /* Text color on hover */
  text-decoration: none; /* Ensures no underline on hover */
}
