.cart {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
}
.cart h1 {
  margin-top: 20px;
  text-transform: capitalize;
  background-color: aqua;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
}
.cartItem {
  width: 400px;
  height: 250px;
  display: flex;

  align-items: center;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  border-radius: 25px;
  margin: 30px;
}

.cartItem img {
  width: 150px;
}

.cartItem .description {
  width: 80%;
  font-size: 30px;
}

.countHandler input {
  width: 60px;
  text-align: center;
  font-weight: bolder;
  font-size: 20px;
}
.countHandler button {
  width: 60px;
  background-color: aqua;
  font-size: 20px;
  border-radius: 20px;
}
.countHandler button:hover {
  background-color: black;
  color: aliceblue;
  border-radius: 10px;
}

.checkout button {
  width: 250px;
  height: 70px;
  background-color: rgb(19, 19, 19);
  color: white;
  border: none;
  border-radius: 8px;
  margin: 10px;
  cursor: pointer;
  font-size: 15px;
}
.checkout p {
  font-size: 20px;
  color: brown;
  font-weight: 600;
}
.checkout button:hover {
  background-color: brown;
  color: aqua;
}
